html {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  text-decoration: none;
  display: block;
}

.nav {
  text-transform: uppercase;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
  line-height: 60px;
}

.nav__item:first-of-type {
  color: #00acdc;
  margin-left: 0;
  position: relative;
}

.nav__item:first-of-type:after {
  content: "";
  color: #00acdc;
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link {
  color: inherit;
  height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  background-color: inherit;
}

.main {
  box-sizing: border-box;
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 1200px) {
  .main {
    padding: 50px 128px;
  }
}

@media screen and (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media screen and (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media screen and (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  font-size: 12px;
  line-height: 18px;
  font-weight: inherit;
  margin: 0;
}

.card__code {
  color: #616070;
  align-self: start;
  margin: 4px 0 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin: 16px 0 0;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  height: 18px;
  margin-top: 24px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-tag {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  vertical-align: middle;
  background-color: #00acdc;
  border-style: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  display: inline-block;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #f3f3f3;
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  align-items: center;
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:last-child {
  background-image: url("star.822d561e.svg");
}

.stars .review {
  font-size: 10px;
  font-weight: 200;
  line-height: 14px;
}
